  width: 100%;
  height: 100%;
  position: relative;
  .addForm{
    position: absolute;
    left: 10px;
    top: 3px;
    width: 300px;
    padding: 10px 15px;
    border-radius: 5px;
    background: #fff;
    z-index: 3;
    text-align: left;
  }
  .analysisList{
    position: absolute;
    right:-500px;
    top:10px;
    height:90%;
    width: 350px;
    background: #fff;
    z-index: 3;
    padding:20px 15px;
    text-align: left;
    border-radius: 5px;
    transition: all 0.8s ease;
    >p{
      margin-top: 10px;
    }
    div{
      padding: 5px 0 10px;
    }
  }
  .active{
    right:10px
  }
}
/* 地图框样式 */
#viewDiv{
  height: 100%;
}
.cursorPen{
  cursor: url("../../assets/projectApproval_images/pen.ico") 0 32,auto;
}
/* 加载中 */
.demo-spin-icon-load{
  animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
  from { transform: rotate(0deg);}
  50%  { transform: rotate(180deg);}
  to   { transform: rotate(360deg);}
}
